<template>
  <div class="memberhome">
    <layout-member :active="3">
      <slides :database="database"></slides>
    </layout-member>
  </div>
</template>
<script>
  import Slides from '../modules/slider';
  import LayoutMember from '../modules/layout_member.vue'
  import {userdata} from '../mock-data/userdata.js'
  export default {
    components:{
      LayoutMember,
      Slides
    },
    data(){
      return{
        userdatas:userdata,
        database:[]
      }
    },
    created(){
      var name = this.$route.params.user;
      if(name&&this.userdatas&&this.userdatas[name]){
        this.database = this.userdatas[name]['photo'];
      }
    }
  }
</script>

<style lang="less">

</style>



